/*
=============== 
Variables
===============
*/

:root {
  --clr-grey-1: hsl(209, 61%, 16%);
  --clr-grey-2: hsl(211, 39%, 23%);
  --clr-grey-3: hsl(209, 34%, 30%);
  --clr-grey-4: hsl(209, 28%, 39%);
  /* grey used for paragraphs */
  --clr-grey-5: hsl(210, 22%, 49%);
  --clr-grey-6: hsl(209, 23%, 60%);
  --clr-grey-7: hsl(211, 27%, 70%);
  --clr-grey-8: hsl(210, 31%, 80%);
  --clr-grey-9: hsl(212, 33%, 89%);
  --clr-grey-10: hsl(210, 36%, 96%);
  --clr-white: #fff;
  --clr-red-dark: hsl(360, 67%, 44%);
  --clr-red-light: hsl(360, 71%, 66%);
  --clr-green-dark: hsl(125, 67%, 44%);
  --clr-green-light: hsl(125, 71%, 66%);
  --clr-black: #222;
  --transition: all 0.3s linear;
  --spacing: 0.1rem;
  --radius: 0.25rem;
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  --max-width: 1170px;
  --fixed-width: 620px;
}

/*
=============== 
Global Styles
===============
*/

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background: var(--clr-grey-10);
  color: var(--clr-grey-1);
  line-height: 1.5;
  font-size: 0.875rem;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4 {
  letter-spacing: var(--spacing);
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 0.75rem;
}
h1 {
  font-size: 2.5rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.25rem;
}
h4 {
  font-size: 0.875rem;
}
p {
  margin-bottom: 1.25rem;
  color: var(--clr-grey-3);
}

/* buttons */
.btn {
  cursor: pointer;
  border: transparent;
  border-radius: var(--radius);
  letter-spacing: var(--spacing);
  padding: 0.375rem 0.8rem;
  box-shadow: var(--light-shadow);
  transition: var(--transition);
  text-transform: capitalize;
  display: inline-block;
}
.option-btn:hover {
  background: #020826;
  box-shadow: var(--dark-shadow);
}
.answer-btn:hover {
  background: #8c7851;
}

.btn-block {
  width: 100%;
}

/* alerts */
.alert {
  padding: 0.375rem 0.75rem;
  margin-bottom: 1rem;
  border-color: transparent;
  border-radius: var(--borderRadius);
}

.alert-danger {
  color: var(--clr-white);
  background: var(--clr-red-dark);
}

.alert-success {
  color: var(--green-dark);
  background: var(--green-light);
}

/* forms */

.form {
  width: 80vw;
  padding: 2rem 2.5rem;
  margin: 2rem auto;
  background: #eaddcf;
  border-radius: var(--radius);
  max-width: var(--max-width);
}
.form p {
  text-align: center;
  color: #020826;
  font-weight: 600;
  font-size: 0.8rem;
}

.form-label {
  display: block;
  font-size: var(--smallText);
  margin: 0.5rem 0;
  text-transform: capitalize;
  letter-spacing: var(--letterSpacing);
  color: #f25042;
  font-weight: 600;
}
.form-input,
.form-textarea {
  width: 100%;
  padding: 0.375rem 0.75rem;
  border-radius: var(--radius);
  border: 1px solid #020826;
  margin-bottom: 0.7rem;
  box-shadow: var(--light-shadow);
}
.btn-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 1.5rem;
}
.option-btn {
  margin-bottom: 0.7rem;
  background: #8c7851;
  color: #fffffe;
  font-size: 1rem;
}
.popularity-btn {
  margin-bottom: 0.7rem;
  background: #f25042;
  color: #fffffe;
  font-size: 1rem;
}
.popularity-btn:hover {
  background: #020826;
}

.popularity-text {
  color: #f25042 !important;
  font-weight: 700;
  font-size: 1.25rem !important;
  padding-top: 2rem;
}
.correct-option {
  color: green;
}
.answer-btn {
  background: #020826;
  color: var(--clr-white);
}

.answer-btn-container {
  display: flex;
  flex-direction: column;
  margin-top: 3rem;
}

.answer-btn-container .btn {
  margin-bottom: 1rem;
}
.form-row {
  margin-bottom: 1rem;
}

.form-textarea {
  height: 7rem;
}

::placeholder {
  font-family: inherit;
  color: var(--clr-grey-1);
  font-size: 0.7rem;
}

.form-alert {
  color: var(--clr-red-dark);
  letter-spacing: var(--spacing);
  text-transform: capitalize;
}

.section {
  width: 90vw;
  margin: 0 auto;
  max-width: var(--max-width);
}

main {
  min-height: 100vh;
  background: #f9f4ef;
}

.form-container {
  margin: 0 auto;
  padding: 2rem 0;
}
.header {
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #020826;
  color: #020826;
}
.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.75);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: var(--transition);
  z-index: -1;
}
.isOpen {
  opacity: 1;
  z-index: 999;
}

.modal-content {
  background: var(--clr-white);
  width: 90vw;
  max-width: var(--fixed-width);
  padding: 3rem;
  border-radius: var(--radius);
  text-align: center;
  position: relative;
}
.modal-content p {
  font-size: 1.5rem;
  text-transform: none;
}
.form h5 {
  color: #020826;
  font-weight: 700;
  font-size: 1rem;
  margin-bottom: 0.8rem;
}
li {
  text-transform: capitalize;
  font-size: 1rem;
}
footer {
  width: 80vw;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Kaushan Script", cursive;
  font-size: 1.35rem;
}
footer p {
  color: #020826;
  font-weight: 800;
}
.logo {
  height: 2.1rem;
  object-fit: cover;
  margin-top: 1rem;
}

@media screen and (min-width: 800px) {
  h1 {
    font-size: 3rem;
  }
  h2 {
    font-size: 2.5rem;
  }
  h3 {
    font-size: 1.75rem;
  }
  h4 {
    font-size: 1rem;
  }
  body {
    font-size: 1rem;
  }
  h1,
  h2,
  h3,
  h4 {
    line-height: 1;
  }
  .section {
    width: 80vw;
  }
  .form {
    width: 50vw;
  }
  footer {
    width: 90vw;
  }
  .form p {
    font-size: 1rem;
  }
}
